.present {
  padding-top: 40px;
  padding-bottom: 40px;
}

.present__wrapper {
  overflow-x: hidden;
}

.present__slogan {
  width: 700px;
  margin-left: -120px;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
}

.present__sogan-txt {
  color: @white;
  font-size: 45px;
  line-height: 1.4;
  text-transform: uppercase;

  &:nth-child(2) {
      text-align: right;
  }
}
