.news-index {
  background-color: white;
  padding-top: 70px;
  padding-bottom: 80px;
}

.news-index__title-wrapper {
  margin-bottom: 30px;
}

.news-index__all-news-link {
  display: none;
}

.news-index__all-news-link-mobile {
  display: block;
  font-size: 17px;
  line-height: 1.3;
  color: @red;
  font-weight: 500;
  position: relative;
  padding-right: 30px;
  width: 128px;
  margin: 0 auto;

  &::after {
    content: "";
    width: 15px;
    height: 10px;
    position: absolute;
    background-image: url("../img/red-arrow.png");
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    top: 6px;
    right: 0;
  }
}

.news-index__title {
  font-size: 30px;
  line-height: 42px;
  color: @black;
  font-weight: 700;
  text-transform: uppercase;
}
