.footer {
  border-top: 1px solid red;
  padding-top: 40px;
  background-color: @gray;
  color: @white;
}

.footer__main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 25px;

  @media screen and (min-width: @extra-large) {
    margin-bottom: auto;
  }
}

.footer__logo-img {
  margin-bottom: 10px;
}

.footer__logo-txt {
  font-size: 20px;
  line-height: 29px;
  color: @black;
}

.footer__list-item {
  margin-bottom: 15px;

  @media screen and (min-width: @extra-large) {
    margin-bottom: 10px;
  }
}

.footer__list-link {
  color: @white;

  &:hover {
    color: @red;
  }

  &:active {
    opacity: 0.8;
  }
}

.footer__main {
  @media screen and (min-width: @medium) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-bottom: 40px;
    border-bottom: 1px solid red;
  }

  @media screen and (min-width: @extra-large) {

  }
}

.footer__list {
  width: 100%;
}

.footer__wrapper-inner:nth-child(1) {
  .footer__rkn-logo {
    display: none;

    @media screen and (min-width: @extra-large) {
      display: block;
    }
  }

  @media screen and (min-width: @medium) {
    flex-grow: 1;
    width: 100%;
  }

  @media screen and (min-width: @extra-large) {
    width: 100%;
    max-width: 270px;
    display: flex;
    flex-direction: column;
    margin-right: 120px;
  }
}

.footer__wrapper-inner:nth-child(2) {
  padding-bottom: 20px;

  @media screen and (min-width: @extra-large) {
    margin-right: 130px;
    max-width: 180px;
  }

  .footer__list-title {
    @media screen and (min-width: @medium) {
      display: block;
      font-size: 20px;
    }
  }

  .footer__list {
    margin-bottom: 20px;
  }

  .footer__rkn-logo {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid red;
    padding-bottom: 20px;

    .footer__rkn-logo-img {
      margin-bottom: 10px;
    }

    @media screen and (min-width: @medium) {
      border: none;
      width: 228px;
      padding-bottom: 0;
    }

    @media screen and (min-width: @extra-large) {
      display: none;
    }
  }
}

.footer__wrapper-inner:nth-child(3) {
  .footer__list {
    display: flex;
    flex-direction: column;
    height: 152px;
    flex-wrap: wrap;

    .footer__list-item {
      width: 48%;
    }
  }

  @media screen and (min-width: @medium) {
    display: block;

    .footer__list-title {
      font-size: 20px;
    }

    .footer__list {
      height: 281px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
    }

    .footer__list-item {
      width: 100%;
      max-width: 228px;
    }
  }

  @media screen and (min-width: @extra-large) {
    width: auto;
    margin-right: auto;
  }
}

.footer__social {
  padding-bottom: 5px;

  @media screen and (min-width: @extra-large) {
    display: flex;
    justify-content: space-between;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.footer__social-list {
  display: flex;
  margin-bottom: 20px;

  @media screen and (min-width: @medium) {
    margin-top: 10px;
  }

  @media screen and (min-width: @extra-large) {
    margin-bottom: 0;
    margin-top: 0;
  }
}

.footer__social-list-item {
  margin-right: 10px;
}

.footer__social-list-link--youtube {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 37px;
    height: 28px;

    background-image: url("../img/youtube-icon.png");
    background-size: 37px 28px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.footer__social-list-link--instagram {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 27px;
    height: 27px;

    background-image: url("../img/instagram-icon.png");
    background-size: 27px 27px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.footer__social-list-link--vkontakte {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 27px;
    height: 27px;

    background-image: url("../img/vk-icon.png");
    background-size: 27px 27px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.footer__social-list-link--telegram {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 27px;
    height: 27px;

    background-image: url("../img/telega.png");
    background-size: 27px 27px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.footer__social-list-link--facebook {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 27px;
    height: 27px;

    background-image: url("../img/facebook-icon.png");
    background-size: 27px 27px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.footer__email-link {
  color: @white;
  font-size: 20px;
  font-weight: bold;
  transition: all ease-in 0.3s;

  &:hover {
    color: @red;
  }

  &:active {
    opacity: 0.8;
  }
}

.footer__wrapper-bottom {
  border-top: 1px solid #4A4E53;
  display: flex;
  flex-direction: column-reverse;
  padding-top: 15px;
  padding-bottom: 15px;

  @media screen and (min-width: @extra-large) {
    flex-direction: row;
    justify-content: space-between;
    border-top: 0;
    position: relative;

    &::after {
      content: "";
      display: block;
      height: 1px;
      width: calc(100vw - 17px);
      transform: translateX(-50%);
      background-color: red;
      position: absolute;
      top: 0;
      left: 50%;
    }
  }
}

.footer__confidentiality {
  margin-bottom: 17px;
  color: @white;
}

.footer__telegramm-link {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  @media screen and (min-width: @extra-large) {
    margin-bottom: 0;
  }
}

.footer__telegramm-icon {
  margin-right: 20px;
}

.footer__telegramm-txt {
  color: @white;
  font-size: 20px;
  font-weight: bold;
  transition: all ease-in 0.3s;

  &:hover {
    color: @red;
  }

  &:active {
    opacity: 0.8;
  }
}

.footer__btn {
  display: none;
}

.footer__btn--mobile {
  display: block;
  text-align: center;
  margin-bottom: 40px;
  order: 5;
}

.footer__silte {
  margin-bottom: 20px;
  order: 6;
  color: @text-color;
}

.footer__wrapper-inner:nth-child(1) {
  order: 1;
  margin-bottom: 40px;
}

.footer__wrapper-inner:nth-child(2) {
  order: 4;
  margin-bottom: 40px;
}

.footer__wrapper-inner:nth-child(3) {
  order: 2;
  margin-bottom: 40px;
}

.footer__wrapper-inner:nth-child(4) {
  order: 3;
  margin-bottom: 40px;
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  margin-top: 20px;

  .footer__mail-link {
    color: @white;
    margin-bottom: 10px;
  }

  .footer__tel-link {
    color: @white;
  }
}
