.header {

}

.header__wrapper {
  padding-top: 18px;
}

.header__navigation-wrapper {
  display: flex;
  justify-content: space-between;
}

.header__wrapper-inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__user-block {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header__user-list {
  display: flex;
  align-items: center;
}

.header__user-list-item {
  margin-right: 15px;
  width: 24px;
  height: 24px;
  position: relative;

  &:nth-child(3) {
    display: none;
  }
}

.header__user-cart--active {
  &::after {
    content: "";
    width: 10px;
    height: 10px;
    display: block;
    background-color: @red;
    position: absolute;
    top: -5px;
    right: -5px;
    border-radius: 50%;
  }
}

.header__navigation {
  width: 100%;
  max-width: 100%;
  display: none;
  margin-top: 40px;
}

.header__navigation-list {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 40px;

  &::after {
    content: "";
    display: block;
    width: 80%;
    height: 2px;
    background-color: #E6E6EC;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__navigation-list-item {
  margin-bottom: 16px;
  padding-left: 40px;
}

.header__navigation-link {
  font-weight: 500;
  color: @black;
  font-size: 28px;
}

.header__contacts {
  display: none;
}

.header__contacts-mobile {
  display: flex;
  flex-direction: column;
  padding-left: 40px;
  margin-bottom: 80px;
  margin-top: 30px;

  .header__mail-link {
    font-size: 17px;
    line-height: 20px;
    color: @black;
    font-family: "Arial", sans-serif;
    margin-bottom: 20px;
  }

  .header__tel-link {
    font-size: 17px;
    line-height: 20px;
    color: @black;
    font-family: "Arial", sans-serif;
  }
}

.header__address-mobile {
  font-size: 15px;
  line-height: 25px;
  color: #8694A1;
  font-family: "Arial", sans-serif;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 25px;
}

.header__logo {
  width: 140px;
  height: 32px;

  @media screen and (min-width: 374px) {
    width: 160px;
  }
}

.header__logo-img {
  width: 160px;
  height: 32px;
  object-fit: cover;
}

.logo-path {
  fill: #fff;
}

.header__search-icon-black {
  display: none;
}

.header__cart-icon-black {
  display: none;
}

.header__user-icon-black {
  display: none;
}

.header__mobile-btn {
  height: 29px;
  width: 30px;
  position: relative;
  background-color: transparent;
  border: none;
  outline: none;
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
  }
}

.header__mobile-btn--gamburger {
  animation: scale 0.3s;

  height: 29px;
  width: 30px;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @white;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: block;
    width: 80%;
    height: 3px;
    background-color: @white;
    position: absolute;
    top: 13px;
    right: 0;
    border-radius: 1px;
  }
}

.header__mobile-btn--close {
  animation: scale2 0.4s;

  &::after {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @black;
    position: absolute;
    top: 13px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @black;
    position: absolute;
    bottom: 13px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: none;
  }
}

.show {
  animation: show 0.4s;
  margin-left: 0px;
}

.hide {
  @media screen and (max-width: @extra-large) {
    animation: hide 0.4s;
    margin-left: -300px;
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale2 {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes show {
  0% {
    margin-left: -300px;
  }

  100% {
    margin-left: 0;
  }
}

@keyframes hide {
  0% {
    margin-left: 0;
  }

  100% {
    margin-left: -300px;
  }
}

.header--index {
  .header__wrapper {
    background:
      url("../img/start-screen-desk-bg.png") 50% no-repeat,
      url("../img/start-screen-shadow.png") 50% no-repeat;
    background-size: cover;
  }
}

.header--active {
  background-color: @white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  .header__wrapper {
    background-image: none;
  }

  .header__search-icon {
    display: none;
  }

  .header__search-icon-black {
    display: block;
  }

  .header__cart-icon {
    display: none;
  }

  .header__cart-icon-black {
    display: block;
  }

  .header__user-icon {
    display: none;
  }

  .header__user-icon-black {
    display: block;
  }

  .logo-path {
    fill: #000;
  }
}

// header__info

.header__info {
  padding-top: 120px;
  padding-bottom: 240px;
}

.header__red-txt {
  text-transform: uppercase;
  color: @red;
  font-weight: 500;
}

.header__info-title {
  color: @white;
  font-size: 28px;
  line-height: 1.4;
  font-weight: 700;
  text-transform: uppercase;

  @media screen and (min-width: 374px) {
    font-size: 41px;
  }
}

.header__info-txt {
  color: @white;
  font-size: 19px;
  line-height: 1.7;
  font-weight: 400;
  margin-bottom: 54px;
}

.header__info-btn {
  width: 100%;
  text-align: center;
}
