* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: @roboto;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  color: @text-color;
  background-color: @light-gray;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
}

.container {
  width: 100%;
  max-width: 576px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;

  @media screen and (min-width: @medium) {
    max-width: 1024px;
  }

  @media screen and (min-width: @extra-large) {
    max-width: 1260px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

a {
  text-decoration: none;
  transition: all ease-in 0.3s;

  &:active {
    opacity: 0.8;
  }
}

p {
  margin: 0;
}

