.news-list {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.news-list__item {
  position: relative;
  min-height: 365px;
  margin-bottom: 20px;
}

.news-list__item-img {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 0;
  height: calc(100% - 1px);
  object-fit: cover;
}

.news-list__item-wrapper {
  background-image: url("../img/news-item-shadow-small.png");
  background-size: cover;
  min-height: 365px;
  position: relative;
  z-index: 2;
  display: flex;
}

.news-list__txt-wrapper {
  margin-top: auto;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 38px;
}

.news-list__date {
  font-size: 15px;
  line-height: 1.7;
  color: @white;
  font-family: "Arial", sans-serif;
}

.news-list__link {
  font-size: 22px;
  line-height: 1.3;
  color: @white;
}
