.btn {
  display: inline-block;
  font-size: 15px;
  line-height: 18px;
  padding-top: 19px;
  padding-bottom: 22px;
  padding-left: 48px;
  padding-right: 48px;
  position: relative;
  color: @white;
  background-color: red;
}

.btn--white {
  color: @white;
  border: 2px solid @white;

  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 25px;
    height: 18px;
    background-image: url("../img/arrow-white-btn.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
  }
}

.btn--black {
  color: @black;
  border: 2px solid @black;

  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 25px;
    height: 18px;
    background-image: url("../img/arrow-black-btn.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
  }
}
