.product-card {
  padding-top: 35px;
  padding-left: 35px;
  padding-right: 48px;
  padding-bottom: 40px;
  background-color: @white;
  margin-bottom: 20px;
}

.product-card__image-wrapper {
  width: 60%;
  margin-left: auto;
  margin-bottom: 32px;
}

.product-card__count {
  font-size: 15px;
  line-height: 1.7;
  color:  #A8ACAF;
  font-family: "Arial", sans-serif;
}

.product-card__title {
  margin-top: 0;
  margin-bottom: 30px;
  color: @black;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.3;
}

.product-card__link {
  color: @red;
  position: relative;
  padding-left: 30px;
  font-size: 0;
  height: 20px;

  @media screen and (min-width: @extra-large) {
    font-size: 15px;
    line-height: 18px;
  }

  &::before {
    content: "";
    display: block;
    width: 20px;
    height: 2px;
    background-color: red;
    position: absolute;
    top: 9px;
    left: 0;
  }

  &::after {
    content: "";
    display: block;
    width: 2px;
    height: 20px;
    background-color: red;
    position: absolute;
    top: 0px;
    left: 9px;
  }
}
