.slider {}

.swiper-button-next {
  display: none;
}

.swiper-button-prev {
  display: none;
}

.slider--about-us {
  padding-bottom: 40px;

  .slider__item-wrapper {
    display: flex;
    flex-direction: column;
  }

  .slider__left-column {
    height: 332px;
    position: relative;
    margin-bottom: 20px;
  }

  .header__mail-icon {
    width: 100%;
    height: 100%;
  }

  .slider__int {
    display: block;
    position: absolute;
    font-size: 200px;
    line-height: 1;
    font-weight: 800;
    color: @red;
    top: 50%;
    left: 50%;
    transform: translate(-60%; -50%);
  }

  .slider__year {
    display: block;
    position: absolute;
    right: -20px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 52px;
    line-height: 1;
    color: @black;
    text-transform: uppercase;
    transform: rotate(-90deg);
  }

  .slider__right-column {

  }

  .slider__title {
    font-size: 25px;
    line-height: 32px;
    color: @black;
    margin-bottom: 20px;
  }

  .slider__txt {
    font-size: 19px;
    line-height: 32px;
  }
}

.swiper-pagination-bullet {
  border-radius: 0;
}

.swiper-pagination-bullet-active {
  background-color: @red;
}

.slider--long {
  padding-bottom: 40px;

  .slider__item {
    height: 150px;
    background-color: @white;
    position: relative;
  }

  .slider__item-img--long {
    width: 85px;
    height: 92px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate( -50%; -50%);
  }
}
